.nav {
	text-align: center;
	height: 100px;
	background-color: rgb(207, 75, 27);
	color: white;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-left: 30px;
	padding-right: 30px;
	padding-top:20px;
	font-size: 25px;
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana,
		sans-serif;
}

.link {
	text-decoration: none;
	color: inherit;
}
.link:hover {
	letter-spacing: .1em;
    border-bottom: 3px solid white;
    padding-left:10px;
}
.main {
	text-align: center;
	font-family: 'Ubuntu', sans-serif;
}
