.form-box{
    width:280px;
    height:520px;
    position:relative;
    margin-left:auto;
    margin-right:auto;
    margin-top:80px;
    background: #FFF;
    padding:15px;
    overflow:hidden;
    border-radius:9px; 
    border: 2px solid rgb(73, 60, 60);
	box-shadow: 0 0 9px 7px #692b139d;
	font-family: 'Ubuntu', sans-serif;
}
.button-box{
    width:220px;
    height:40px;
    margin:35px auto;
    position:relative;
    box-shadow: 0 0 20px 9px #ff61241f;
    border-radius: 30px;
    border:2px solid rgb(214, 5, 5);
    display: flex;
    align-items: center;
} 
.toggle-btn{
    padding:10px 25px;
    cursor: pointer;
    background: transparent;
    border: 0;
    outline: none;
    position: relative;
    font-size: 20px;
} 
.btn{
    top:0;
    left:0;
    position: absolute;
    width:110px;
    height: 100%;
    background: linear-gradient(to right, #ff105f,#ffad06);
    border-radius:30px;
    transition: .5s;
}
.input-group{
    top:180px;
    position: absolute;
    width:280px;
    transition: .5s;
    left:10px;
    
}
.input-field{
    width:100%;
    padding:10px 0;
    margin:5px 0;
    border-left: 0;
    border-right: 0;
    border-top: 0;
    border-bottom: 1px solid #999;
    outline:none;
    background: transparent;
	font-size: 20px;
}
.submit-btn{
    width:85%;
    padding:10px 30px;
    cursor: pointer;
    display:block;
	margin:auto;
	margin-top:25px;
    background: linear-gradient(to right, #ff105f,#ffad06);
	font-size:20px;
	font-weight: bold;
	letter-spacing: .1em;
	color:rgb(23, 66, 66);
    border:0;
    outline:none;
    border-radius: 30px;
}
#Login{
    left:-480px;
}
#Signup{
    left:auto;
}
